@tailwind base;
@tailwind components;
@tailwind utilities;

::-webkit-scrollbar {
      width: 12px;
    }

    ::-webkit-scrollbar-thumb {
      background-color: rgb(185,254,77) ; /* Change this to your desired thumb color */
      border-radius: 6px;
    }

    ::-webkit-scrollbar-track {
      background-color: black ; /* Change this to your desired track color */
    }