.about{
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 70%;
    margin: auto;
    min-height: 100vh;
    justify-content: center;
    vertical-align: middle;
    gap: 20px;
    max-height: max-content;
    margin-top: 30px;
}
.aboutImage{
    width: 50px;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(185,254,77);
    padding: 10px;
    border-radius: 100%;
    box-shadow: 10px 10px 0px 0px rgba(0, 0, 0, 0.282);
}
.aboutHeading{
    color: black;
    font-size: 50px;
    font-weight: 600;
    text-align: center ;
}
.aboutContent{
    color: black;
    font-size: 20px;
    font-weight: 400;
    text-align: justify;
}
@media (max-width: 1000px){
    .about{
        width: 90%;
        text-align: justify;
    }
    .aboutContent{
        font-size: 15px;
    }
    
}
